.form-submit {
  background-color: $btn-primary-background;
  border-color: $btn-primary-background;
  color: $btn-primary-color;

  &:hover {
    color: $btn-secondary-color-hover;
    background: $btn-primary-background-hover;
  }
  &:focus,
  &:active {
    background: $btn-primary-background-focus;
  }
}

.contact-us {
  background: url(../images/form-bg.jpg) no-repeat center / cover;

  h2 {
    color: $white;
  }

  .flex-col {
    display: flex;
    flex-direction: column;
  }

  .form-input {
    margin-bottom: 10px;
    width: 100%;
  }

  textarea {
    height: 100%;
    min-height: 150px;
    resize: none;
  }
}

#custom-region {
  display: none;
}